/* main */
.app-wraper {
  margin: 0 auto;
  display: grid;
  max-width: 1200px;
  grid-template-areas: 
  "h h"
  "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wraper-content{
  grid-area: c;
  background-color: rgb(126, 126, 126);
}
